.calendar {
  display: flex;
  min-height: 100%;
  margin: 0 auto;
  position: relative;
  display: flex;
  justify-content: center;
  max-width: 1200px;
  min-width: 750px;
  width: 100%;

  &__inner {
    padding: 5px;
    display: flex;
    flex-direction: column;
    width: 100%;
    border-radius: 8px;
    border: 2px solid var(--color-gray-primary);
  }

  &__header-wrapper {
    margin-bottom: 8px;
  }

  &__body-wrapper {
  }
}
