.calendar__day {
  cursor: pointer;
  border: 1px solid var(--color-gray-primary);
  padding: 6px;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  transition: all 0.2s;
  overflow: hidden;


  &-header {
    font-size: 12px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 4px;
    background-color: var(--color-gray-light-04);
    padding: 2px 6px;
    border-radius: 2px;
    font-weight: 600;

    &-number {
      &--first {
        color: var(--color-red-1);
      }
    }
  }

  &--current {
    background-color: var(--color-active-06);
  }

  &:hover {
    background-color: var(--color-active-02);
  }

  &-todo {
    &-list {
      list-style: none;

    }
    &-item {
      margin-bottom: 4px;
      overflow-x: hidden;
      border-radius: 2px;
      background-color: var(--color-gray-light-04);
      padding: 0 2px;
    }
  }

}
